import { motion } from "framer-motion"
import { useState,useEffect } from "react"
import { useWindowSize } from "../../utils/useWindowSize"
import ProjectPopUp from "../Project/ProjectPopUp"

function PopUpTile({index,title,descriptions,item,bg}) {

	const [isOpen,setIsOpen] = useState(false)
	const [isHover,setIsHover] = useState(false)
	const [width] = useWindowSize()
	const [device, setDevice] = useState("")

	useEffect(()=>{
		if(width < 500)
			setDevice("SMALL")
		if(width <=1030 && width >=500)
			setDevice("MEDIUM")
		if(width >1030)
			setDevice("LARGE")
	},[width])	

	const CircleVariants ={
		"DEFAULT":{
			width:"4vw",
			height:"4vw",
			borderRadius:"10rem"
		},
		"HOVER":{
			width:"30%",
			height:"100%",
			borderRadius:"5rem"
		},
	}

	const TextVariants ={
		"DEFAULT":{
			color:"#1F1F1F"
		},
		"HOVER":{
			color:"#F4F9FF"
		},
	}


	return (
		<>
			<div 
				className="relative flex border-b-[1.5px] justify-center  border-charcoal  gap-10 w-full items-center cursor-pointer bg-contain h-fit py-5"
				onClick={()=>setIsOpen(true)}
				onMouseEnter={() => setIsHover(true)}
				onMouseLeave={() => setIsHover(false)}
				style={index===0?{borderTop:"1.5px solid"}:{borderTop:"0px solid"}}
			>
				{device!=="SMALL"&&
				<motion.div 
					className={`absolute w-full h-full left-0 object-fill mx-2 z-0 ${bg}`}
					alt={title}
					animate={isHover?CircleVariants["HOVER"]:CircleVariants["DEFAULT"]}
					transition={{
						duration:0.5,
						ease:"easeInOut",
						type: "spring",
					}}
				/>}

				<div 
					className="w-full h-full px-10 small:px-5 pl-[8%] small:pl-5  flex justify-start items-center z-10  gap-10"
				>
					<motion.p
						animate={isHover?TextVariants["HOVER"]:TextVariants["Default"]} 
						className="text-[1.8vw]  leading-tight font-[300] text-left w-[25%] small:w-[50%] small:text-base">
						{title}
					</motion.p>

					<ul 
						className="w-[70%] small:text-[1.1vh] text-[0.8rem] list-disc">
						<li>{descriptions?.at(0).slice(0,1000)}</li>
						{
							device!=="SMALL"&&
							(descriptions?.at(0).length<400?
								<li>{descriptions?.at(1)}</li>:
								null)
						}
					</ul>

				</div>
			</div>
			
			<ProjectPopUp
				project={item}
				setIsOpen={setIsOpen}
				isOpen={isOpen}
			/>
		</>
	)
}

export default PopUpTile